@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Poppins);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.Layout {
    height: 100vh;
    display: flex;
}

.MenuOpen {
    flex: 0.7 1;
    background-color:white; 
    z-index: 2;
    box-shadow: 0.1em 0 0.4em 0 #C2CFE0;  
}

.closeMenu{
    flex: 0.1 1;
    background-color:white; 
    z-index: 2;
    box-shadow: 0.1em 0 0.4em 0 #C2CFE0;  
}

.WithoutMenu {
    flex: 4 1;
    display: flex;
    flex-direction: column;
}
.Layout main {
    flex: 9 1;
}

.Layout input{
    flex: 1 1;
}
.Layout input, .Layout form button {
    border: none;
    outline: none;
    background-color: white;
    display: flex;
    height: 2.4em;
    align-items: center;
}

.Layout form {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 2.7em;
}

.Layout form i {
    padding: 0.5em 0.5em 0.5em 1.5em;
    color: #C2CFE0;
    font-size: 1.2em;
}

.Layout form img {
    padding: 0.5em 1.5em 0.5em 0.5em;
    color: #C2CFE0;
    font-size: 1.2em;
}

.Link {
    display: flex;
    align-items: center;
}


@font-face {
    font-family: 'Material Icons Outlined';
    font-style: normal;
    font-weight: 400;
    src: url(https://fonts.gstatic.com/s/materialiconsoutlined/v22/gok-H7zzDkdnRel8-DQ6KAXJ69wP1tGnf4ZGhUce.woff2) format('woff2');
  }

.material-icons-outlined {
    font-family: 'Material Icons Outlined';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -webkit-font-feature-settings: 'liga';
    -webkit-font-smoothing: antialiased;
  }
.Drawer {

}

.Drawer ul {
    list-style: none;
    padding-left: 1em;
}

.Drawer li {
    padding-bottom: 1em;
}

a:hover, .act {
    color: #109CF1;
}

a {
    font-family: Poppins;
    font-size: 13px;
    text-decoration: none;
    color: #C2CFE0;
    display: flex;
    align-items: center;
}

a text{
    color: #334D6E;
}

.act text{
    color: #109CF1;
}
.MenuText {
    margin-left: 1em;
}

.line {
    height: 0.05em;
    width: 100%;
    background-color: #C2CFE0;
}


.MenuToggle {
    cursor: pointer;
    margin-left: 1.3em;
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 1em;
}

.MenuToggle:hover {
    opacity: 0.7;
}

.open {
    font-family: Poppins;
    font-size: 11px;
    color: #90A0B7;
}

.MenuToggle text {
    margin-left: 1em;
}
.Nickname {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: #109CF1;
    display: flex;
    align-items: center;    
    border-bottom: 0.05em solid #C2CFE0;
    padding-right: 0.5em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
}

.ProfileName {
    font-family: Poppins;
    font-size: 14px;
    color: #192A3E;
}

.EmailAdr {
    font-family: Poppins;
    font-size: 11px;
    color: #90A0B7;
}

.MainProfile {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 1em;
    margin-bottom: 3em;
}

.InfoPadding {
    padding-left: 1em;
    padding-right: 1em;
}
.Search {
    height: 2.7em;
    width: 100%;
    border-bottom: 0.07em solid #C2CFE0;
}
.Dashboard {
    display: flex;
    justify-content: center;
    background-color: #E5E5E5;
    height: 100%;
}
.Tasks {
    background-color: #E5E5E5;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Table {
    border-collapse: collapse;
    border-radius: 0.5em;
    min-width: 95%;
    cursor: pointer;
    overflow: hidden;
}

.Table tr {
    border: 0.1em solid #E5E5E5;
    height: 3.5em;
}

.Table td {
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 19px;
    color: rgba(51, 77, 110, 0.5);
    letter-spacing: 0.01em;
    text-align: left;
}

.Name {
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 22px;
    letter-spacing: 0.01em;
    color: #323C47;
}

.Table img {
    border-radius: 50%;
    width: 2em;
    height: 2em;
    display: block;
}

.ImgTh {
    width: 3em;
}

.panel {
    min-width: 95%;
    display: flex;
}
.panel text {
    flex: 1 1;
    margin: 3em 0em 2em 0em;
    font-family: Poppins;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.01em;
    color: #6A707E;
    display: flex;
    align-items: flex-end;
}

.panel button {
    padding: 1em 2em 1em 2em;
    margin: 3em 0em 2em 0em;
    background: #109CF1;
    box-shadow: 0px 4px 10px rgba(16, 156, 241, 0.24);
    border-radius: 4px;
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 13px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.01em;
    color: #FFFFFF;
    cursor: pointer;
    border: none
}

.panel button:hover {
    background: #34AFF9;
    box-shadow: 0px 8px 16px rgba(52, 175, 249, 0.2);
    border-radius: 4px;
}

.panel button:active {
    background: #098EDF;
    box-shadow: 0px 2px 6px rgba(9, 142, 223, 0.3);
    border-radius: 4px;
}

.panel button:disabled {
    background: #C2CFE0;
    border-radius: 4px;
    cursor: default;
}

option {
    color: #109CF1;
}

select {
    width: 2em;
    border: none;
    background-color: transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: url(/WEB/static/media/Arrow.6a02ab9f.svg) no-repeat right;
    color: #109CF1;
}

/* CAUTION: IE hackery ahead */
select::-ms-expand { 
    display: none; /* remove default arrow on ie10 and ie11 */
}
.ActiveTr {
    background-color: #F2F9FF;
}

.Tr {
    background-color: white;
}

.custom-checkbox, .custom-checkbox-count {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.custom-checkbox+label, .custom-checkbox-count+label {
    display: flex;
    align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 13px;
    padding-left: 1em;
}

  .custom-checkbox+label::before, .custom-checkbox-count+label::before {
    width: 1.5em;
    height: 1.5em;
    flex-shrink: 0;
    flex-grow: 0;
    border: 0.15em solid #adb5bd;
    border-radius: 25%;
    background-repeat: no-repeat;
    background-position: center center;
    content: attr(count);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFFFFF;
    font-family: Poppins;
    font-style: normal;
    font-weight: bold;
    font-size: 9px;
    line-height: 13px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.01em;
}

.custom-checkbox:checked+label::before {
    border-color: #109CF1;
    background-color:  #109CF1;
    background-image: url(/WEB/static/media/checked.9ec786cf.svg);
}

.custom-checkbox-count:checked+label::before {
    border-color: #109CF1;
    background-color:  #109CF1;
}

.countSelected {
    padding-left: 3em;
    padding-right: 3em;
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 0.01em;
    color: #334D6E;
}
